//Â≠êÁªÑ‰ª∂‰º†ÈÄíÁªôÁà∂ÁªÑ‰ª∂ Â¶Ç this.$emit('change', this.count)
var s10 = new Vue({
	el: '#s10',
	data: {
		n: 100 
	},
	methods: {
		changeTotal: function(value){
			this.n = value
		}	
	},
	components: {
		child: {
			data: function(){
				return { count: 100 }
			},
			template: `
				<div>
					<button @click="increase">+1</button>
					<button @click="decrease">-1</button>
				</div>
			`,
			methods: {
				increase: function(){
					this.count += 1	
					this.$emit('change', this.count)
				},
				decrease: function(){
					this.count -= 1
					this.$emit('change', this.count)
				}
			}		
		}
	}
})

