// Ëá™ÂÆö‰πâÊåá‰ª§ directive „ÄÇË∑ü component Á±ª‰ºº„ÄÇ‰∏ãÈù¢ÂÅö‰∏Ä‰∏™ÊâìÂºÄÈ°µÈù¢Â∞±Ëé∑ÂèñÁÑ¶ÁÇπÁöÑÊ°à‰æã„ÄÇ
/*
directive Â≠òÂú®Âá†‰∏™ hooks ÂáΩÊï∞:
bind: Âè™Ë∞ÉÁî®‰∏ÄÊ¨°ÔºåÊåá‰ª§Á¨¨‰∏ÄÊ¨°ÁªëÂÆöÂà∞ÂÖÉÁ¥†Êó∂Ë∞ÉÁî®
inserted: Ë¢´ÁªëÂÆöÂÖÉÁ¥†ÊèíÂÖ•Áà∂ËäÇÁÇπÊó∂Ë∞ÉÁî®ÔºàÁà∂ËäÇÁÇπÂ≠òÂú®Âç≥ÂèØË∞ÉÁî®Ôºå‰∏çÂøÖÂ≠òÂú®‰∫é document ‰∏≠Ôºâ„ÄÇ

update: Ë¢´ÁªëÂÆöÂÖÉÁ¥†ÊâÄÂú®ÁöÑÊ®°ÊùøÊõ¥Êñ∞Êó∂Ë∞ÉÁî®ÔºåËÄå‰∏çËÆ∫ÁªëÂÆöÂÄºÊòØÂê¶ÂèòÂåñ„ÄÇÈÄöËøáÊØîËæÉÊõ¥Êñ∞ÂâçÂêéÁöÑÁªëÂÆöÂÄºÔºå
ÂèØ‰ª•ÂøΩÁï•‰∏çÂøÖË¶ÅÁöÑÊ®°ÊùøÊõ¥Êñ∞„ÄÇ

componentUpdated: Ë¢´ÁªëÂÆöÂÖÉÁ¥†ÊâÄÂú®Ê®°ÊùøÂÆåÊàê‰∏ÄÊ¨°Êõ¥Êñ∞Âë®ÊúüÊó∂Ë∞ÉÁî®„ÄÇ

unbind: Âè™Ë∞ÉÁî®‰∏ÄÊ¨°ÔºåÊåá‰ª§‰∏éÂÖÉÁ¥†Ëß£ÁªëÊó∂Ë∞ÉÁî®„ÄÇ
*/

