var s21 = new Vue({
	el: '#s21',
	data: {
		show: true
	},
	methods: {
		switchShow: function(){
			this.show = !this.show
		}
	},
	components: {
		'child': {
			props: ['show'],
			render: function(createElement){
				var imgsrc
				if(this.show){
					imgsrc = 'img/001.jpg'
				}else{
					imgsrc = 'img/002.jpg'
				}
				return createElement('img', {
					attrs: {
						src: imgsrc
					}
				})
			}
		}
	}
})

// input ËæìÂÖ•‰ªÄ‰πàÂ∞±ÊòæÁ§∫‰ªÄ‰πà
var s22 = new Vue({
	el: '#s22',
	data: {
		name: 'jack'
	},
	methods: {
		showName: function(value){
			this.name = value
		}
	},
	components: {
		'child': {
			props: ['name'],
			render: function(createElement){
				let self = this
				return createElement('input', {
					domProps: {
						value: self.name
					},
					on: {
						input: function(value){
							self.$emit('input', event.target.value)
						}
					}
				})
			}
		}
	}
})

